import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Chip from '@mui/material/Chip';
import { useEffect } from 'react';
import { useState } from 'react';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};



function getStyles(name, personName, theme) {
    
    return {
        fontWeight:
        personName.indexOf(name) === -1
            ? theme.typography.fontWeightRegular
            : theme.typography.fontWeightMedium,
    };
}

export default function PeopleSelector(props) {
    const theme = useTheme();
    const [userList, setUserList] = useState()
    const [selectedUsers, setSelectedUsers] = useState();
    //const [personName, setPersonName] = useState([]);

    useEffect(() => {
        var axios = require('axios');
        var data = '';

        var config = {
            method: 'get',
            url: 'http://localhost:3001/api/persons',
            data : data
        };

        axios(config)
        .then(function (response) {
            console.log(response.data);
            let tempList = [];
            for (let i = 0; i < response.data.length; i++){
                tempList.push(response.data[i].name);
            }
            props.setAllUsers(response.data)
            setUserList(tempList);
        })
        .catch(function (error) {
            console.log(error);
        });
    }, [])

  const handleChange = (event) => {
    const {
      target: { value },
    } = event;
    props.setPersonName(
      // On autofill we get a stringified value.
      typeof value === 'string' ? value.split(',') : value,
    );
  };

  return (
    <div>
      <FormControl sx={{ width: 435, marginTop: "30px" }}>
        <InputLabel id="demo-multiple-chip-label">People</InputLabel>
        <Select
          labelId="demo-multiple-chip-label"
          id="demo-multiple-chip"
          multiple
          value={props.personName}
          onChange={handleChange}
          input={<OutlinedInput id="select-multiple-chip" label="people" />}
          renderValue={(selected) => (
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
              {selected.map((value) => (
                <Chip key={value} label={value} />
              ))}
            </Box>
          )}
          MenuProps={MenuProps}
        >
          {userList && userList.map((name) => (
            <MenuItem
              key={name}
              value={name}
              style={getStyles(name, props.personName, theme)}
            >
              {name}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
  );
}